p,
a,
h1,
h2,
h3,
h4,
li,
tr {
  font-family: "Montserrat";
}
li {
  list-style-type: none;
}

body {
  margin: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  /* border: 1px solid red; */
}
.popup {
  border-radius: 8px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -ms-border-radius: 8px;
  -o-border-radius: 8px;
  background-color: #ffffff;
  max-width: 1289px;
  width: 100%;
  margin: 0 auto;
}
.container {
  max-width: 1214px;
  width: 100%;
  margin: 0 auto;
  padding: 33px 20px 0;
  @media all and(max-width: 800px)
{
    padding: 30px 10px;
  }
  &__icons {
    margin-bottom: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}


.container_second {
  max-width: 1070px;
  width: 100%;
  margin: 0 auto;
  padding: 0 5px;
}

a {
  text-decoration: none;
}

button:hover {
  opacity: 0.9;
}

