
.authorization_block{
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #000000;
  max-width: 310px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 52px;
  display: block;
  // display: none;
  }
  @media (max-width: 470px) {
    .authorization_block {
      font-size: 12px;
      margin-bottom: 30px;
    }
   }
  
  .authorization_text-left {
    border-bottom: 1px solid #6B6B6B;
    padding-bottom: 8px;
    margin-right: 10px;
    cursor: pointer;
  }
  .authorization_text-right {
    margin-right: 6px;
    cursor: pointer;
  }
  
.authorization_text-bottom {
  font-family: 'Montserrat';
font-style: normal;
font-weight: 300;
font-size: 14px;
line-height: 17px;
color: #363636;
}
.authorization_wrap {
  max-width: 635px;
  width: 100%;
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 50px;
}
@media (max-width: 470px) {
  .authorization_wrap {
    grid-gap: 30px;
  }
 }
 @media (max-width: 420px) {
  .authorization_wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
 }
.authorization_text-bottom:nth-child(3) {
  margin-left: 22px;
  position: relative;
}
.authorization_button:first-child,
.authorization_button:hover,
.authorization_button:active{
  color: #FFFFFF;
  background: #1E1E1E;
 border-radius: 20px;
}
.authorization_button {
  padding: 10px 40px;
width: 188px;
height: 40px;
background: #FFFFFF;
border: 1px solid #000000;
border-radius: 20px;
font-family: 'Montserrat';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 20px;
cursor: pointer;
display: flex;
align-items: center;
}
.authorization_button_block {
  max-width: 640px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 40px 0 173px;
}
@media (max-width: 470px) {
  .authorization_button_block {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 40px 0 40px;
  }
  .authorization_button:first-child {
    margin-bottom: 20px;
  }
 }
.authorization_text-bottom:nth-child(2) {
  text-align: right;
}
@media (max-width: 470px) {
  .authorization_text-bottom:nth-child(2) {
    text-align: left;
    font-size: 14px;
line-height: 17px;
  }
 }

.authorization_text-bottom:nth-child(3)::after {
  content: "*";
  position: absolute;
  color: red;
  width: 10px;
    height: 10px;
    top: 5px;
    left: -19px;
    font-size: 20px;}

.authorization_text-input:first-child::before {
  content: "*";
  position: absolute;
  color: red;
  width: 10px;
    height: 10px;
    top: 5px;
    left: 110px;
    font-size: 18px;
}

@media (max-width: 470px) {
  .authorization_text-input:first-child::before {
      top: 4px;
      left: 76px;
      font-size: 16px;
  }
 }

