
.authorization_text-input {
  font-family: 'Montserrat';
font-style: normal;
font-weight: 400;
font-size: 12px;
line-height: 15px;
color: #6B6B6B;
margin-bottom: 5px;
position: relative;
}
@media (max-width: 470px) {
  .authorization_text-input {
    font-size: 8px;
  }
 }
.authorization_input {
  max-width: 635px;
  width: 100%;
  height: 50px;
  border: 1px solid #000000;
border-radius: 10px;
padding: 0 0 0 30px;
}
.authorization_input::placeholder {
  font-family: 'Montserrat';
font-style: normal;
font-weight: 300;
font-size: 16px;
line-height: 20px;
color: #959595;
}